<template>
  <div class="guidelines">
    <NavHeader :links=this.navLinks />
    <div class="content has-gap">
      <div class="loading" v-if="!guideline"></div>
    <div class="guideline" v-if="guideline">
      <div class="title"><h1 class="unselectable">{{guideline.title}}</h1><img v-if="guideline.approvements.length >= 5" src="@/assets/images/verified.png" class="verified_badge" title="Diese Guideline ist verifiziert"><router-link :to="'/editor?g=' + this.$route.query.g"><img src="@/assets/images/edit.svg" alt="Diese Guideline bearbeiten" v-if="this.$store.getters.isLoggedIn && this.guideline.author_id == this.$store.getters.getUser.id" id="editButton"></router-link></div>
      <h2 class="unselectable">von <router-link :to="'/user?u=' + guideline.author_id" class="unselectable">{{guideline.name}}</router-link></h2>
      <p v-html="guideline.text" id="text"></p>
      <div class="bibliography unselectable" v-if="guideline.bibliography && guideline.bibliography.length > 0">
        <h3>Quellen</h3>
        <p v-html="guideline.bibliography"></p>
      </div>
      <p id="last_update" class="unselectable">letzte Aktualisierung: {{guideline.last_update}}</p>
      <button id="add_annotation_button" @click="toggleAddAnnotation" v-if="!addAnnotation"><img src="@/assets/images/edit.svg" alt="Diese Guideline bearbeiten" v-if="this.$store.getters.isLoggedIn && !addAnnotation" id="editButton">Annotation verfassen</button>
      <button id="add_annotation_button" class="active" @click="toggleAddAnnotation" v-if="addAnnotation">Jetzt Textstelle mit Cursor markieren!</button>
      <div class="approvements unselectable">
        <h3 v-if="guideline.approvements && guideline.approvements.length > 0">durch <b>{{guideline.approvements.length}}</b> Experten besttigt</h3>
        <h3 v-if="guideline.approvements && guideline.approvements.length < 1">noch nicht besttigt</h3>
        <div class="approvers">
          <router-link v-for="approvement in guideline.approvements" :to="'/user?u=' + approvement.id" :key="approvement.userId" :value="approvement.userId"><img :src=approvement.image><p class="expert_name">{{approvement.name}}</p></router-link>
        </div>
        <button id="approve_button" @click="approveGuideline" v-if="this.$store.getters.isLoggedIn && this.$store.getters.getUser && this.$store.getters.getUser.is_expert && !this.alreadyApproved && this.guideline.author_id != this.$store.getters.getUser.id"><img src="@/assets/images/checkmark.png">Guideline Besttigen</button>
        <button id="approve_button" class="revert" @click="revertApproval" v-if="this.$store.getters.isLoggedIn && this.$store.getters.getUser && this.$store.getters.getUser.is_expert && this.alreadyApproved"><img src="@/assets/images/cancel.svg" style="filter: invert(100%);">Besttigung zurckziehen</button>
      </div>
      <div id="annotation" :class="{anno_loading: annotationLoading, active: activeAnnotation}">
        <div v-if="activeAnnotation">
          <img :src="activeAnnotation.image">
        </div>
        <div class="annotation_content" v-if="activeAnnotation">
          <h2>{{activeAnnotation.name}}</h2>
          <p>{{activeAnnotation.annotation_text}}</p>
        </div>
        <div class="annotation_voting" v-if="activeAnnotation">
          <div class="vote_button" id="upvote" @click="handleAnnotationVote" :class="{active : activeAnnotation.userVote === 'u'}"></div>
          <p v-if="activeAnnotation">{{activeAnnotation.score}}</p>
          <div class="vote_button" id="downvote" @click="handleAnnotationVote" :class="{active : activeAnnotation.userVote === 'd'}"></div>
        </div>
        <button @click="deleteAnnotation(activeAnnotation.annotation_id)" v-if="activeAnnotation && activeAnnotation.author_id == this.$store.getters.getUser.id"><img src="@/assets/images/delete.svg"></button>
      </div>
      <div id="annotation_editor" :class="{active: selectedText}">
        <h2>Annotation Verfassen</h2>
        <q>{{selectedText}}</q>
        <textarea rows="10" v-model="annotationText"></textarea>
        <div style="display: flex; gap: 10px;">
          <button class="send_annotation_button cancel" @click="selectedText = null">Abbruch</button>
          <button class="send_annotation_button" @click="saveAnnotation">Senden</button>
        </div>
      </div>
    </div>
  </div>
  </div>
</template>

<script>

import NavHeader from "@/components/navHeader.vue";
import moment from "moment";
import hljs from "highlight.js";
import 'highlight.js/styles/a11y-dark.css';
import AuthService from "@/services/AuthService";
import { debounce } from 'debounce';

export default {
  name: 'HomeView',
  components: {NavHeader},
  data(){
    return {
      navLinks: [
        {
          link: "/guidelines",
          name: "guidelines"
        }
      ],
      selectedText: null,
      activeAnnotation: null,
      annotationLoading: false,
      addAnnotation: false,
      timeOfAnnotationOpen: null,
      annotationText: null,
      guideline: null,
      guidelineTEMPLATE: {
        title: "Optimieren des DOM fr Screenreader",
        author: "Thomas Schmitz",
        author_id: 1,
        last_update: "06.07.2023",
        text: "HTML ist eine Auszeichnungssprache und muss von <a id='999' class='annotationLink'>jedem professionellen Webentwickler beherrscht werden</a>. Ein standardkonformes HTML-Grundgerst erleichtert Software wie Browsern und Hilfsmitteln behinderter Nutzer die korrekte Aufbereitung einer Webseite. Bei der Barrierefreiheit geht es vor allem um ein strukturiertes und linearisierbares HTML-Grundgerst. Erst wenn ein Dokument mit einer Auszeichnungssprache semantisch aufbereitet wird, lassen sich weitere Prinzipien von Webstandards, etwa die Trennung von Inhalt und Layout, effizient umsetzen. Diese Seite bietet einen knappen berblick ber verfgbare Layout-Techniken. HTML bietet drei grundlegende Layouttechniken.\n\nStandardkonformitt setzt u.a. die Trennung von Inhalt und Layout voraus, d.h. alle Aspekte von Layout und Formatierung sind ber CSS vorzunehmen; damit wird die Darstellung von Webseiten auf diverse Endgerte erst mglich. Tabellen und Frames sind ungeeignet fr eine barrierefreie Webgestaltung, weil sie die visuelle Anordnung am Bildschirm vorgeben. Bevor ein CSS-Design umgesetzt werden kann, muss die Linearisierbarkeit und Strukturierung der Inhalte im HTML vorgenommen werden. Eine weitere Voraussetzung fr Barrierefreiheit ist die Bercksichtigung der Prinzipien des Progressive Enhancement, d.h. Inhalte mssen immer genutzt werden knnen, auch wenn ergnzende Techniken wie Bilder oder JavaScript im Browser des Nutzers nicht untersttzt werden oder abgeschaltet sind. Progressive Enhancement besagt, dass HTML stets die Grundtechnik fr alle Informationen und Funktionen ist und alle weiteren Techniken, seien es CSS oder JavaScript, aber auch der Einsatz von Grafiken und anderen Objekten als optional zu betrachten sind. Die Entwicklung von Webstandards war vor allem in den 1990er rasant. Zwischenzeitlich hat sich die Verabschiedung von Webstandards stark verndert, weil verffentlichte Webstandards auch den Praxistest bestehen mssen.\n\nDer Einsatz von gltigem HTML ist ein Teilaspekt von Barrierefreiheit. Vor allem ist der korrekte Syntax die Voraussetzung fr valides HTML  ein Qualittsmerkmal fr Webentwickler. Mit dieser Tabelle knnen alle jemals spezifizierten HTML-Elemente in HTML 2.0, HTML 3.2, HTML 4.01, XHTML, HTML5 und HTML 5.1 nebeneinander verglichen werden. Einige Elemente gibt es nicht mehr, einige haben es nie wirklich geschafft und einige werden in der Zukunft eine neue Bedeutung bekommen. Fr die Webentwicklung wird mit Accessible Rich Internet Applications (ARIA) zunchst ein Satz von Attributen bestimmt, die HTML, SVG und andere Auszeichnungssprachen mit mehr Semantik ausstatten. Diese betreffen u.a. Webanwendungen: Mit ARIA-Attributen knnen Widgets so ausgezeichnet werden, dass sie semantisch solchen Komponenten entsprechen, ber die ein Betriebssystem verfgt, mit der Folge, dass sie von Screenreadern und anderen Hilfsmitteln besser bedient werden knnen. ARIA umfasst aber weitaus mehr als einige Attribute. Zunchst ist ARIA eine Spezifikation an Browser und Hilfsmittel. Darber hinaus hat der Webstandard einen merklichen Einfluss auf HTML5 gehabt. Insgesamt geht es aber darum, dass Screenreader und andere Hilfsmittel sich auf eine spezielle Schnittstelle des Betriebssystems sttzen und dass ARIA die Technik ist, die die Webentwicklung bentigt, um diese Schnittstelle sinnvoll zu bedienen.",
        verified: true,
        bibliography: [],
        approvements:[
          {
            userId: 101,
            name: "Theresa Sauer",
            image: "data:image/png;base64,"
          },
          {
            userId: 102,
            name: "Maike Schtze",
            image: "data:image/png;base64,"
          },
          {
            userId: 103,
            name: "Thomas Mller",
            image: "data:image/png;base64,"
          },
          {
            userId: 104,
            name: "Kirsten Meier",
            image: "data:image/png;base64,"
          },
          {
            userId: 105,
            name: "Dana Schmitz",
            image: "data:image/png;base64,"
          }
        ]
      },
      alreadyApproved : false
    }
  },
  created(){
    if (this.$route.query.g == 3){
      this.guideline = {
        title: "Guideline mit Code",
        name: "Jim Halpert",
        author_id: 2,
        last_update: "26.03.2023",
        text: "HTML ist eine Auszeichnungssprache und muss von jedem professionellen Webentwickler beherrscht werden. Ein standardkonformes HTML-Grundgerst erleichtert Software wie Browsern und Hilfsmitteln behinderter Nutzer die korrekte Aufbereitung einer Webseite. <a id='999' class='annotationLink'>Bei der Barrierefreiheit geht es vor allem um ein strukturiertes und linearisierbares HTML-Grundgerst.</a> Erst wenn ein Dokument mit einer Auszeichnungssprache semantisch aufbereitet wird, lassen sich weitere Prinzipien von Webstandards, etwa die Trennung von Inhalt und Layout, effizient umsetzen. Diese Seite bietet einen knappen berblick ber verfgbare Layout-Techniken. HTML bietet drei grundlegende Layouttechniken, und hier folgt etwas Code: \n\n<pre><p>HTML</p><code><xmp><!DOCTYPE html>\n" +
            "<html>\n" +
            "<body>\n" +
            "\n" +
            "<h1>This is heading 1</h1>\n" +
            "<h2>This is heading 2</h2>\n" +
            "<h3>This is heading 3</h3>\n" +
            "<h4>This is heading 4</h4>\n" +
            "<h5>This is heading 5</h5>\n" +
            "<h6>This is heading 6</h6>\n" +
            "\n" +
            "</body>\n" +
            "</html>\n" +
            "\n</xmp></code></pre>\n\nFr das Syntax Higlighting wird dabei highlight.js eingebunden. Theoretisch kann zwischen einer vielzahl an Farbschemata gewhlt werden, doch hier wirkte es naheliegend, das 'a11y-dark' Thema zu whlen. Javascript oder jegliche andere Programmiersprachen sind selbstverstndlich auch mglich, viel Spa mit etwas Beispielcode aus der Vue-Komponente der aktuellen Seite:\n\n<pre><p>JS</p><code><xmp>for(let i in annoLinks){\n" +
            "      if (typeof(annoLinks[i]) === \"object\"){\n" +
            "        annoLinks[i].addEventListener(\"click\", this.getAnnotation);\n" +
            "      }\n" +
            "    }</xmp></code></pre>",
        bibliography: [],
        verified: true,
        approvements:[
          {
            userId: 101,
            name: "Theresa Sauer",
            image: "data:image/png;base64,"
          },
          {
            userId: 102,
            name: "Maike Schtze",
            image: "data:image/png;base64,"
          },
          {
            userId: 103,
            name: "Thomas Mller",
            image: "data:image/png;base64,"
          },
          {
            userId: 104,
            name: "Kirsten Meier",
            image: "data:image/png;base64,"
          },
          {
            userId: 105,
            name: "Dana Schmitz",
            image: "data:image/png;base64,"
          }
        ]
      }
    } else if (this.$route.query.g == 4) {
      this.guideline = {
        title: "Guideline mit Bild und Quellenangabe",
        name: "Pam Beesly",
        author_id: 3,
        last_update: "12.04.2023",
        text: "Es gibt viele schne Bilder auf dieser Welt, und diese sollen natrlich auch im System einbindbar sein. Dafr kann es verschiedenste Grnde geben, aber das Sprichwort <i>Ein Bild sagt mehr als tausend Worte</i> <b>[1]</b> wird nicht lgen, daher sollten Bilder definitiv problemlos in einer Guideline einbindbar sein. Fr dieses Beispiel gehe ich davon aus, dass das Bild als Weblink existiert und nicht als Base64, aber das kann problemlos gendert werden, sollten wir hier eine andere Architekturentscheidung treffen.\n\n<img src='https://www.th-koeln.de/mam/bilder/hochschule/organisation/standorte/fittosize_705_0_234e2e5854dd50684360c17e33d3353f_th_koln_campus_gummersbach_sebastian_hopp.jpg'><b>[2]</b>\n\nDie TH Kln (Technische Hochschule Kln) ist eine renommierte deutsche Hochschule mit einem breiten Spektrum an Studiengngen in den Bereichen Technik, Wirtschaft, Sozialwissenschaften und Kultur. Mit modernen Einrichtungen, engagierten Dozenten und einer praxisorientierten Ausbildung bietet die TH Kln ihren Studierenden eine erstklassige akademische Erfahrung. Durch enge Verbindungen zur Industrie und zahlreiche Forschungsprojekte ermglicht die TH Kln ihren Absolventen einen nahtlosen bergang in die Arbeitswelt. Entdecke die vielfltigen Mglichkeiten, die die TH Kln bietet, um deine beruflichen Ziele zu erreichen und deine Leidenschaft in die Tat umzusetzen.",
        bibliography: [
          {
            id: 1,
            text: "'Ein Bild sagt mehr als tausend Worte' Wikipedia-Artikel, abgerufen am 11.07.2023. https://de.wikipedia.org/wiki/Ein_Bild_sagt_mehr_als_tausend_Worte"
          },
          {
            id: 2,
            text: "Bild von der Website der TH Kln, abgerufen am 11.07.2023. https://www.th-koeln.de/hochschule/campus-gummersbach_76496.php"
          }
        ],
        verified: true,
        approvements: [
          {
            userId: 105,
            name: "Dana Schmitz",
            image: "data:image/png;base64,"
          }
        ]
      }
    } else {
      this.getGuideline()
    }
  },
  mounted() {
  },
  methods: {
    async getGuideline(){
      let result = await AuthService.getGuideline({"guideline_id": this.$route.query.g})
      if(result.msg){
        result.msg.last_update = moment(result.msg.last_update).format("DD.MM.Y, HH:mm")
        this.guideline = result.msg
        this.guideline.approvements.forEach(approvement => {
          if(approvement.id == this.$store.getters.getUser.id){
            this.alreadyApproved = true
          }
        })
      }
    },
    async getAnnotation(input){
      this.annotationLoading = true
      document.removeEventListener("click", this.handleClick)
      document.removeEventListener("scroll", this.handleClick)
      if(this.activeAnnotation && this.activeAnnotation.id === parseInt(input.srcElement.id)){
        document.getElementById("annotation").classList.remove("active")
        this.activeAnnotation = null;
      } else {
        let data = {
          "annotation_id": input.srcElement.id
        }
        if(this.$store.getters.isLoggedIn) data.user_id = this.$store.getters.getUser.id

        document.getElementById("annotation").style.top = (input.srcElement.getBoundingClientRect().y + 30).toString() + "px";
        document.getElementById("annotation").style.left = ((input.srcElement.getBoundingClientRect().x / 2)).toString() + "px";
        document.addEventListener("click", this.handleClick)
        document.addEventListener("scroll", this.handleClick)


        let result = await AuthService.getAnnotation(data)
        this.activeAnnotation = result.msg
        this.annotationLoading = false
        this.timeOfAnnotationOpen = moment();
        //this.handleVoteButtonColor()
      }
    },
    buildAnnotationLinks(){
      let annoLinks = document.getElementsByClassName("annotationLink")
      for(let i in annoLinks){
        if (typeof(annoLinks[i]) === "object"){
          annoLinks[i].addEventListener("click", this.getAnnotation);
        }
      }
    },
    async handleAnnotationVote(input){
      let upvote = input.srcElement.id === "upvote" ? 1 : 0

      let data = {
        "annotation_id": this.activeAnnotation.annotation_id,
        "user_id": this.$store.getters.getUser.id,
        "upvote": upvote
      }

      await AuthService.voteAnnotation(data)

      data = {
      "annotation_id": this.activeAnnotation.annotation_id
      }

      if(this.$store.getters.isLoggedIn) data.user_id = this.$store.getters.getUser.id

      let result = await AuthService.getAnnotation(data)
      this.activeAnnotation = result.msg
    },
    handleClick(evt){
      if(this.activeAnnotation != null && document.getElementById("annotation").classList.contains("active") && this.timeOfAnnotationOpen !== null && moment().diff(this.timeOfAnnotationOpen, 'seconds', true) > 0.1){
        const annotationEL = document.getElementById("annotation");
        let clickedEL = evt.target;
        do {
          if(clickedEL === annotationEL) {
            // do nothing
            return;
          }
          // Go up the DOM
          clickedEL = clickedEL.parentNode;
        } while (clickedEL);
        // This is a click outside.
        this.activeAnnotation = null;
      }
    },
    async approveGuideline(){
      let data = {
        "expert_id" : this.$store.getters.getUser.id,
        "guideline_id": this.$route.query.g
      }

      let result = await AuthService.approveGuideline(data)

      if(result.msg == "Success"){
        this.$router.go()
      }
    },
    async revertApproval(){
      let data = {
        "expert_id" : this.$store.getters.getUser.id,
        "guideline_id": this.$route.query.g
      }

      let result = await AuthService.revertApproval(data)

      if(result.msg == "Success"){
        this.$router.go()
      }
    },
    async saveAnnotation(){
      let data = {
        "guideline_id" : this.$route.query.g,
        "author_id": this.$store.getters.getUser.id,
        "text": this.annotationText,
        "selected_text": this.selectedText
      }

      let result = await AuthService.saveAnnotation(data)
      if(result.msg == "Success"){
        this.$router.go()
      }
    },
    async deleteAnnotation(id){
      let data = {
        "guideline_id": this.$route.query.g,
        "annotation_id": id,
        "author_id": this.$store.getters.getUser.id
      }

      let result = await AuthService.deleteAnnotation(data)
      if(result.status == 200){
        this.$router.go()
      }
    },
    getSelectionText() {
      if(!this.selectedText){
        let text = "";
        if (window.getSelection) {
          text = window.getSelection().toString();
        } else if (document.selection && document.selection.type != "Control") {
          text = document.selection.createRange().text;
        }

        if(text.length < 200){
          this.selectedText = text
        }
        this.addAnnotation = false
      }
    },
    toggleAddAnnotation(){
      if(this.addAnnotation){
        this.addAnnotation = false
      } else {
        this.addAnnotation = true
        document.addEventListener("selectionchange", debounce(this.getSelectionText,2000), {once: true});
      }
    }
  },
  updated(){
    hljs.highlightAll()
    this.buildAnnotationLinks()
  }
}
</script>
