                <img v-if="guideline.approvements && guideline.approvements.length >= 5" src="@/assets/images/verified.png" class="verified_badge" title="Diese Guideline ist verifiziert">
                <img v-if="!guideline.approvements || guideline.approvements.length < 1" src="@/assets/images/warning.png" class="no_approvements" title="Diese Guideline wurde noch nicht besttigt">
              </div>
              <h3 v-if="guideline.approvements && guideline.approvements.length > 0">durch <b>{{guideline.approvements.length}}</b> Experten besttigt</h3>
              <h3 v-if="guideline.approvements && guideline.approvements.length < 1">noch nicht besttigt</h3>
            </div>
          </div>
          <p>{{truncate(stripHTML(guideline.text))}}</p>
