          approvements:[
            {
              userId: 101,
              name: "Theresa Sauer",
              image: "data:image/png;base64,"
            },
            {
              userId: 102,
              name: "Maike Schtze",
              image: "data:image/png;base64,"
            },
            {
              userId: 103,
              name: "Thomas Mller",
              image: "data:image/png;base64,"
            },
            {
              userId: 104,
              name: "Kirsten Meier",
              image: "data:image/png;base64,"
            },
            {
              userId: 105,
              name: "Dana Schmitz",
              image: "data:image/png;base64,"
            }
          ]
        },
        {
          guideline_id: 2,
          title: "Strfaktoren vermeiden",
          teaser: "Die Fhigkeit der Nutzer, Inhalte zu lesen und zu bedienen, darf keinesfalls beeintrchtigt werden, wenn die Seite barrierefrei sein soll. In der Praxis werden Seiten oft \"im Wesentlichen barrierefrei\" oder \"barrierearm\" umgesetzt, aber manchmal sind genau die Aspekte, die nicht barrierefrei umgesetzt werden, so problematisch, dass der gesamte Webauftritt nicht genutzt werden kann.",
