        },
        {
          guideline_id: 5,
          title: "Reingelegt  Per Tastatur in die Falle",
          teaser: "Die Tastaturbedienung gehrt zu den entscheidenden Kriterien des barrierefreien Webdesigns. Die Vermeidung von Tastaturfallen wird dabei in den Web Content Accessibility Guidelines (WCAG) 2.0 eine besondere Gewichtung gegeben. Die Vermeidung von Tastaturfallen ist eine von vier Erfolgskriterien, die fr eine Konformitt immer erfllt sein mssen.",
          approvements: [

          ]
